<script>
import countriesArr from "../assets/data/countries";
import gendersArr from "../assets/data/genders";

export default {
	data() {
		return {
			accountForm: this.$form.createForm(this, { name: "accountForm" }),
			current: 0,
			gender: null,
			isLoading: false,
			loadingMessage: "Creating Account",
			steps: [
				{
					title: "A little bit about yourself."
				},
				{
					title: "Account information."
				}
			],
			rules: {
				required: [{ required: true, message: "This field is required!", trigger: "blur" }],
				name: [{ required: true, message: "This field is required!", trigger: "blur" }],
				dob: [
					{
						type: "integer",
						message: "This field is required!",
						trigger: "blur"
					},
					{
						type: "integer"
					}
				],
				u: [
					{
						required: true,
						message: "Username is required.",
						trigger: "blur"
					},
					{
						type: "email",
						message: "Your username needs to be an email!",
						trigger: "blur"
					}
				],
				p: [
					{
						required: true,
						message: "Password is required!",
						trigger: "blur"
					}
				],
				confirmPassword: [
					{
						required: true,
						message: "Confirm your password",
						trigger: "blur"
					}
				],
				date: [],
				countries: [{ required: true, message: "We need a country!" }]
			},
			genders: gendersArr,
			countries: countriesArr
		};
	},
	methods: {
		changeGender(val) {
			this.gender = val;
		},
		submitForm() {
			this.accountForm.validateFields((err, values) => {
				if (!err) {
					this.isLoading = true;

					this.$client.api
						.post(
							"/users",
							{
								status: "active",
								first_name: values.firstName,
								last_name: values.lastName,
								role: 3,
								email: values.username,
								password: values.password,
								gender: values.gender,
								other_gender: values.gender === "other" ? values.otherGender : null,
								country: values.country,
								dob_year: values.dob,
								email_notifications: values.newsletter
							},
							{
								access_token: process.env.VUE_APP_API_TOKEN
							}
						)
						.then((res) => {
							// tell the user
							this.$message.success(`Account created. Welcome ${res.first_name}!`);

							// login
							this.$client
								.login({
									email: values.username,
									password: values.password
								})
								.then(() => {
									// redirect to cards page
									this.$router.push({ name: "cards" });
								})
								.catch((err) => {
									this.$message.error(err.message);
								});
						})
						.catch((err) => {
							this.$message.error(err.message);
						})
						.then(() => {
							this.isLoading = false;
						});
				} else {
					setTimeout(() => {
						this.accountForm.resetFields();
					}, 4000);
				}
			});
		},
		onSearch(searchText) {
			this.countriesSearch = !searchText;
		}
	}
};
</script>
